:host {
  display: contents;
}

input[type="file"] {
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  background-color: #fff;
  transition: border-color 0.3s ease;
  cursor: pointer;
}

input[type="file"]:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
}

input[type="file"]:disabled {
  background-color: #f5f5f5;
  color: #6c757d;
  cursor: not-allowed;
}

input[type="file"].invalid {
  border-color: #dc3545;
}

input[type="file"].invalid:focus {
  border-color: #dc3545;
  box-shadow: 0 0 0 2px rgba(220, 53, 69, 0.25);
}

label {
  font-weight: 500;
  margin-bottom: 4px;
  color: #333;
  display: block;
}

.file-size-limit {
  font-weight: 400;
  font-size: 12px;
  color: #6c757d;
  font-style: italic;
}

input[type="file"]::file-selector-button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 3px;
  padding: 4px 12px;
  margin-right: 8px;
  cursor: pointer;
  font-size: 14px;
}

input[type="file"]::file-selector-button:hover {
  background-color: #0056b3;
}

/* Additional styling for multiple files indication */
input[type="file"][multiple]::after {
  content: " (multiple files supported)";
  font-size: 12px;
  color: #6c757d;
  margin-left: 8px;
}
