import { Component, EventEmitter, Host, Prop, Event, h } from '@stencil/core';
import { iFormField } from '../../models/form-field.model';

@Component({
  tag: 'multifile-field',
  styleUrl: 'multifile-field.css',
  shadow: false,
  scoped: false,
})
export class MultifileField {
  @Prop({ mutable: false }) field: iFormField;
  @Prop() isInvalid: boolean;
  @Prop() isDisabled: boolean;
  @Prop() isHidden: boolean;
  @Event({ bubbles: true, composed: true }) valueChanged: EventEmitter<{ key: string; value: any }>;
  @Event({ bubbles: true, composed: true }) fieldBlurred: EventEmitter<{ key: string; }>;

  private onValueChange(e: Event) {
    const files = (e.target as HTMLInputElement).files;
    
    if (files && files.length > 0) {
      const fileArray = Array.from(files);
      
      this.valueChanged.emit({ key: this.field.key, value: fileArray });
    } else {
      this.valueChanged.emit({ key: this.field.key, value: [] });
    }
  }

  private handleBlur() {
    this.fieldBlurred.emit({ key: this.field.key });
  }

  render() {
    const maxSizeMB = 'todo';
    
    return (
      <Host>
        <input
          id={this.field.key}
          type="file"
          multiple
          disabled={this.isDisabled}
          accept={this.field.placeholder || '*/*'}
          class={`${this.isInvalid ? 'invalid' : ''}`}
          onInput={(e) => this.onValueChange(e)}
          onBlur={this.handleBlur.bind(this)}
        />

        {this.field.label && (
          <label htmlFor={this.field.key}>
            {this.field.label}
            {maxSizeMB && <span class="file-size-limit"> (max {maxSizeMB}MB per file)</span>}
          </label>
        )}

        <slot></slot>
      </Host>
    );
  }
}
